<script>
import tableComponent from "@/components/Table/Table.vue";
import Pagination1 from "@/components/Pagination1/Pagination1.vue";

// 將搜尋內容轉小寫後，篩選符合結果
const toLower = (text) => {
  return text.toString().toLowerCase();
};
const searchByName = (items, term) => {
  if (term) {
    return items.filter(
      (item) =>
        toLower(item.company).includes(toLower(term)) ||
        toLower(item.applier).includes(toLower(term)) ||
        toLower(item.censorer).includes(toLower(term)) ||
        toLower(item.country).includes(toLower(term)) ||
        toLower(item.status).includes(toLower(term))
    );
  }
  return items;
};

export default {
  name: "accountCensorManagement-component",
  components: { tableComponent, Pagination1 },
  data() {
    return {
      search: "", // 搜尋內容
      tableColumns: [
        { id: "index", label: "項次", width: "70" },
        { id: "company", label: "公司全稱(簡稱)統一編號", width: "380" },
        { id: "applier", label: "申請日期(申請者)", width: "220" },
        { id: "censorer", label: "審核日期(審核者)", width: "220" },
        { id: "country", label: "公司國籍", width: "135" },
        { id: "status", label: "狀態", width: "120" },
      ],
      data: [],
      tableData: [], // 表格資料 (All)
      tableList: [], // 表格資料 (單頁)
    };
  },
  methods: {
    async getAccCensorData() {
      try {
        const res = await this.$store.dispatch("getAccCensorData");
        let data = res.data.data;
        this.data = JSON.parse(JSON.stringify(data));

        // 將獲取的資料，做顯示前加工處理
        this.data.forEach((e, index) => {
          e.index = index + 1;
          e.company = `${e.s_Name} (${e.tel}) ${e.s_Id}`;
          e.applier = `${e.apply_Date}(${e.apply_Name})`;
          e.censorer = `${e.approve_date2}(${e.approve_Name})`;
          e.status = "等待審核";
        });
        this.tableData = this.data;
        this.tableList = this.tableData.slice(0, 10);
      } catch (err) {
        console.log(err);
      }
    },

    // 篩選搜尋之內容，並呈現表格第一頁
    async searchInput(evt) {
      if (evt.code === "Enter" || evt.code === "NumpadEnter") {
        const val = evt.target.value;
        this.tableData = searchByName(this.data, val);
        this.tableList = this.tableData.slice(0, 10);
        this.$store.state.currentPage = 1;
      }
    },
    // 依據頁數，更新對應的 tableList 資料
    changePage(page) {
      this.$store.state.currentPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.tableList = this.tableData.slice(cur, last);
    },
  },
  async mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;

    // 打 API 取得資料，呈現表格第一頁
    this.getAccCensorData();
    this.$store.state.currentPage = 1;
  },
};
</script>

<template src="./accountCensorManagement.html"></template>
<style lang="sass" scoped src="../../../../public/styles/custom.sass"></style>
